@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@200&family=Koulen&family=Noto+Sans:ital,wght@0,300;0,400;0,700;1,600&family=Open+Sans:wght@300&family=Roboto:wght@300&family=Rowdies:wght@700&display=swap);
:root {
  --fontNotoSans: 'Noto Sans', sans-serif;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}
*, *:before, *:after {
  box-sizing: inherit;
  font-family: var(--font);
}
body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
ol, ul {
  list-style: none;
}
img {
  max-width: 100%;
  height: auto;
}
/* stylelint-disable order/properties-alphabetical-order */
* {
  font-family: var(--fontNotoSans);
}

